/**
 * Custom application mixins available through out the app
 */

.root {
  width: 200px;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  background-color: #fff;
  color: #4d4d4d;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 75px;
  width: 100%;
  background: linear-gradient(to right, #eb3349, #f45c43);
}

.logo a img {
  width: 140px;
}

.nav {
  padding: 10px 0;
  overflow-y: auto;
}

