/**
 * Custom application mixins available through out the app
 */

.headerLink a {
  display: flex;
  color: #666;
  align-items: center;
  text-decoration: none;
  cursor: pointer;
}

.headerLink a:hover,
.headerLink a:active,
.headerLink a:focus {
  font-weight: 600;
}

.headerLink > a {
  position: relative;
  padding: 0 15px;
  line-height: 55px;
  justify-content: space-between;
}

.headerLink > a:hover {
  font-weight: 300;
  background-color: #f9f6f2;
}

.headerLink > a img {
  width: 15px;
  margin-right: 15px;
}

.arrow {
  color: #ccc;
  transition: 0.3s;
  right: 0;
}

.arrowActive {
  transform: rotate(-90deg);
}

.headerLinkActive span {
  color: #eb3349;
  font-weight: 600;
}

.panel {
  border: none;
  box-shadow: none;
  margin: 0;
  border-radius: 0;
  background: rgba(0, 0, 0, 0.1);
}

.panel div {
  padding: 0;
}

.panel ul {
  padding-left: 0;
  font-size: 0.9rem;
}

.panel ul li {
  padding: 3px 0;
  list-style: none;
}

.panel ul a {
  padding: 4px 20px 4px 44px;
}

.panel ul a:hover,
.panel ul a:active,
.panel ul a:focus {
  font-weight: 600;
}

