/**
 * Custom application mixins available through out the app
 */

.root :global .location-selector {
  width: 100%;
  height: 220px;
  border: 1px dashed #bbb;
  background-color: #fff;
  position: relative;
}

.root :global .location-selector .bit {
  transition: background-color 0.15s ease-in-out;
  background-color: #cccbca;
  cursor: pointer;
  position: absolute;
}

@media (prefers-reduced-motion: reduce) {
  .root :global .location-selector .bit {
    transition: none;
  }
}

.root :global .location-selector .bit:hover {
  background-color: #ccc;
}

.root :global .location-selector .bit.top,
.root :global .location-selector .bit.bottom {
  height: 20%;
  width: 20%;
  margin: 0 40%;
}

.root :global .location-selector .bit.top {
  top: 0;
}

.root :global .location-selector .bit.bottom {
  bottom: 0;
}

.root :global .location-selector .bit.right,
.root :global .location-selector .bit.left {
  height: 20%;
  width: 20%;
  margin-left: 0;
  margin-right: 0;
}

.root :global .location-selector .bit.right {
  right: 0;
}

.root :global .location-selector .bit.left {
  left: 0;
}

